@font-face{
  font-family: 'Sanford-0103_demo' ;
  src: url('./fonts/Sanford-0103_demo.ttf'); 
}
body{
  margin: 0px;
  padding: 0px;
  font-family: 'Sanford-0103_demo';
  height: 100%;
  background-color: #ffffff;
  background-image: url("../img/overlay.png"), -moz-linear-gradient(60deg, rgba(255, 165, 150, 0.5) 5%, rgba(0, 228, 255, 0.35)), url("../img/bg.jpg");
  background-image: url("..img/overlay.png"), -webkit-linear-gradient(60deg, rgba(255, 165, 150, 0.5) 5%, rgba(0, 228, 255, 0.35)), url("../img/bg.jpg");
  background-image: url("../img/overlay.png"), -ms-linear-gradient(60deg, rgba(255, 165, 150, 0.5) 5%, rgba(0, 228, 255, 0.35)), url("../img/bg.jpg");
  background-image: url("../img/overlay.png"), linear-gradient(60deg, rgba(255, 165, 150, 0.5) 5%, rgba(0, 228, 255, 0.35)), url("../img/bg.jpg");
  background-repeat: repeat, no-repeat, no-repeat;
  background-size: 100px 100px, cover, cover;
  background-position: top left, center center, bottom center;
  background-attachment: fixed, fixed, fixed;
}

/* Main */

.main {
  -moz-transform-origin: 50% 50%;
  -webkit-transform-origin: 50% 50%;
  -ms-transform-origin: 50% 50%;
  transform-origin: 50% 50%;
  -moz-transform: rotateX(0deg);
  -webkit-transform: rotateX(0deg);
  -ms-transform: rotateX(0deg);
  transform: rotateX(0deg);
  -moz-transition: opacity 1s ease, -moz-transform 1s ease;
  -webkit-transition: opacity 1s ease, -webkit-transform 1s ease;
  -ms-transition: opacity 1s ease, -ms-transform 1s ease;
  transition: opacity 1s ease, transform 1s ease;
  padding: 4.5em 3em 3em 3em ;
  background: #ffffff;
  border-radius: 4px;
  cursor: default;
  max-width: 100%;
  opacity: 0.95;
  position: relative;
  text-align: center;
  width: 80%;
  border-radius: 20px;
  margin-top: 50px;
  margin-left: 100px;
  font-family: 'Sanford-0103_demo';
 }

  .main .avatar {
      position: relative;
      display: block;
      margin-bottom: 1.5em;
  }

      .main .avatar img {
          display: block;
          margin: 0 auto;
          border-radius: 100%;
          box-shadow: 0 0 0 1.5em #ffffff;
      }

      .main .avatar:before {
          content: '';
          display: block;
          position: absolute;
          top: 50%;
          left: -3em;
          width: calc(100% + 6em);
          height: 1px;
          z-index: -1;
          background: #c8cccf;
      }
      .main h1{
        font-size: 30px;
      }
      .main p{
        font-size: 25px;
      }
    .efectsoft2{
      margin-top: 100px;
    }
  


  body.is-preload .main {
      -moz-transform: rotateX(15deg);
      -webkit-transform: rotateX(15deg);
      -ms-transform: rotateX(15deg);
      transform: rotateX(15deg);
      opacity: 0;
  }

  
  .efecto h2 {
    position: relative;
    display: block;
    padding: 5px;
  }
  
  .efecto h2::before {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 2px;
    background: linear-gradient(to right, #b47dcd, #e878a2, #eb85ab);
    z-index: 1;
    transform: scaleX(0);
    transform-origin: left;
    transition: transform 0.5s ease-in-out;
  }
  
  .efecto h2:hover::before {
    transform: scaleX(1);
  }
  
  
  /* ANIMATIONS
  –––––––––––––––––––––––––––––––––––––––––––––––––– */
  .efecto[data-animation="to-left"] h2::before {
    transform-origin: right;
  }
  
  .efecto[data-animation="center"] h2::before {
    transform-origin: center;
  }
  
  .efecto[data-animation="bonus"] h2::before {
    transform-origin: right;
  }
  
  .efecto[data-animation="bonus"] h2:hover::before {
    transform-origin: left;
    transform: scaleX(1);
    transition-timing-function: cubic-bezier(0.2, 1, 0.82, 0.94);
  }
  

  /**Efecto de soft */
  .efectsoft h2 {
    position: relative;
    display: block;
    padding: 10px;
  }
  .efectsoft .border {
    position: absolute;
    left: 0;
    background: currentColor;
    transition: transform var(--transition-duration) ease-in-out;
  }
  
  .efectsoft .border-top,
  .efectsoft .border-bottom {
    width: 100%;
    height: 1px;
    transform: scaleX(0);
  }
  
  .efectsoft .border-left,
  .efectsoft .border-right {
    width: 1px;
    height: 100%;
    transform: scaleY(0);
  }
  
  .efectsoft .border-top,
  .efectsoft .border-left,
  .efectsoft .border-right {
    top: 0;
  }
  
  .efectsoft .border-bottom {
    bottom: 0;
    transform-origin: bottom right;
  }
  
  .efectsoft .border-top {
    transform-origin: top left;
  }
  
  .efectsoft .border-left {
    transform-origin: bottom left;
  }
  
  .efectsoft .border-right {
    left: auto;
    right: 0;
    transform-origin: top right;
  }
  
  .efectsoft h2:hover .border-top,
  .efectsoft h2:hover .border-bottom {
    transform: scaleX(1);
  }
  
  .efectsoft h2:hover .border-left,
  .efectsoft h2:hover .border-right {
    transform: scaleY(1);
  }
  
  .efectsoft h2:hover .border-right {
    transition-delay: var(--transition-delay);
  }
  
  .efectsoft h2:hover .border-bottom {
    transition-delay: calc(var(--transition-delay) * 2);
  }
  
  .efectsoft h2:hover .border-left {
    transition-delay: calc(var(--transition-delay) * 3);
  }
  
  
  /* ANIMATIONS
  –––––––––––––––––––––––––––––––––––––––––––––––––– */
  .efectsoft[data-animation="diagonal"] .border-left {
    transform-origin: top left;
  }
  
  .efectsoft[data-animation="diagonal"] .border-right,
  .efectsoft[data-animation="diagonal"] .border-bottom {
    transform-origin: bottom right;
  }
  
  .efectsoft[data-animation="diagonal"] h2:hover [class^=border]{
    transition-delay: 0s;
  }

  
//Barra progresiva
h1{
	font-size:20px;
}

h1 p{
	background:#222;
	color:#fff;
}

.clear{clear:both;}

#boton{
	width:120px;
	height:30px;
}

progress[value] {
	/* Elimino la apariencia por defecto */
	-webkit-appearance: none;
	-moz-appearance: none;
	appearance: none;
	
	/* Quito el borde que aparece en Firefox */
	border: none;

	/* Añado mis propios estilos */
	width: 270px;
	height: 30px;
	
	
/* 	Estos estilos solo se aplicaran al fondo de la barra en mozilla */
	overflow:hidden;
	border:1px inset #666;
	background-color:#D8D8D8;
	border-radius : 20px ; 
}

.progress{
	float:left;
	margin-right:50px;
  margin-bottom:20px;
  width: 400px;
  padding-left: 30px; 
}

.progress span{
	
	font-weight:bold;
	

}

/* Compatibilidad de color en Firefox, Chrome y Safari */
progress::-moz-progress-bar{ 
	background: #D2B5F7;
	background: rgba(210, 181, 247,1);
	background: -moz-linear-gradient(left, rgba(210, 181, 247,1) 0%, rgba(139, 47, 253,1) 100%);
	background: -webkit-gradient(left top, right top, color-stop(0%, rgba(210, 181, 247,1)), color-stop(100%, rgba(139, 47, 253,1)));
	background: -webkit-linear-gradient(left, rgba(210, 181, 247,1) 0%, rgba(139, 47, 253,1) 100%);
	background: -o-linear-gradient(left, rgba(210, 181, 247,1) 0%, rgba(139, 47, 253,1) 100%);
	background: -ms-linear-gradient(left, rgba(210, 181, 247,1) 0%, rgba(139, 47, 253,1) 100%);
	background: linear-gradient(to right, rgba(210, 181, 247,1) 0%, rgba(139, 47, 253,1) 100%);
	filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ffaf4b', endColorstr='#ff920a', GradientType=1 );	
	border-radius : 20px ; 
	} 
progress::-webkit-progress-value { 
	background: #D2B5F7;
	background: rgba(210, 181, 247,1);
	background: -moz-linear-gradient(left, rgba(210, 181, 247,1) 0%, rgba(139, 47, 253,1) 100%);
	background: -webkit-gradient(left top, right top, color-stop(0%, rgba(210, 181, 247,1)), color-stop(100%, rgba(139, 47, 253,1)));
	background: -webkit-linear-gradient(left, rgba(210, 181, 247,1) 0%, rgba(139, 47, 253,1) 100%);
	background: -o-linear-gradient(left, rgba(210, 181, 247,1) 0%, rgba(139, 47, 253,1) 100%);
	background: -ms-linear-gradient(left, rgba(210, 181, 247,1) 0%, rgba(139, 47, 253,1) 100%);
	background: linear-gradient(to right, rgba(210, 181, 247,1) 0%, rgba(139, 47, 253,1) 100%);
	filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ffaf4b', endColorstr='#ff920a', GradientType=1 );
	border-radius : 20px ; 	

}
progress::-webkit-progress-bar { 
	background-color:#D8D8D8;
	box-shadow: 0 2px 5px rgba(0, 0, 0, 0.25) inset;
	border-radius : 20px ; 
		
	
}

/*Responsive*/
@media screen and (max-width: 736px) {
  .main {
     width: 80%;
     margin-left: 50px;
     margin-top: 150px;
  }
  .main h1{
    font-size: 20px;
  }
  .main p{
    font-size: 15px;
  }
  .efectsoft2{
    margin-top: -60px;
  }

  .main .avatar:before {
    left: -2em;
    width: calc(100% + 4em);
  }
  .progress{
    width: 200px;
    padding-left: 0px; 
  }
  
}